import * as React from "react"
import styled from "styled-components"

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  min-height: 100vh;
  position: relative;
`


// markup
const IndexPage = () => {

  const isBrowser = () => typeof window !== 'undefined';

  const imgBrowser = () =>{
    if(isBrowser()){
      return window.innerWidth
    }return 'falla pana rafa'
  }

  return (
    <Wrapper>
    {
     imgBrowser()
    }
    </Wrapper>
  )
}

export default IndexPage
